<template>
	<div>
		<div class="gstc-wrapper" ref="gstc"></div>
	</div>
</template>

<script>
import GSTC from 'gantt-schedule-timeline-calendar';
import { Plugin as TimelinePointer } from 'gantt-schedule-timeline-calendar/dist/plugins/timeline-pointer.esm.min.js';
import 'gantt-schedule-timeline-calendar/dist/style.css';
let gstc, state;
// helper functions
/**
 * @returns { import("gantt-schedule-timeline-calendar").Rows }
 */
function generateRows(data) {
	const rows = {};
	for (let item of data) {
		const id = GSTC.api.GSTCID(item.id.toString());
		rows[id] = {
			id,
			label: item.title,
		};
	}
	return rows;
}
/**
 * @returns { import("gantt-schedule-timeline-calendar").Items }
 */
function generateItems(data) {
	const items = {};
	for (let item of data) {
		if (!item.dueDate) item.dueDate = new Date();
		const id = GSTC.api.GSTCID(item.id.toString());
		const rowId = GSTC.api.GSTCID(item.id);
		let start = GSTC.api.date(item.dueDate).subtract(7, 'day');
		let end = GSTC.api.date(item.dueDate);
		items[id] = {
			id,
			label: item.title,
			rowId,
			time: {
				start: start,
				end: end,
			},
		};
	}
	return items;
}
// main component
export default {
	name: 'GSTC',
	props: ['data'],
	mounted() {
		/**
		 * @type { import("gantt-schedule-timeline-calendar").Config }
		 */
		const config = {
			licenseKey:
				'====BEGIN LICENSE KEY====\||U2FsdGVkX18sqkXSD/vmKlKBpGFMo1zlFHlzO3mh8KdixyWXCdXRklvHGFAuEiL4kRgUmsKDTN/HS1sAz3Ox1Y+dAluLfpGpIjZX/P+Gi+E=\\n====END LICENSE KEY====',
			plugins: [TimelinePointer()],
			list: {
				columns: {
					data: {
						[GSTC.api.GSTCID('label')]: {
							id: GSTC.api.GSTCID('label'),
							width: 200,
							data: 'label',
							header: {
								content: 'Label',
							},
						},
					},
				},
				rows: generateRows(this.data),
			},
			chart: {
				items: generateItems(this.data),
			},
		};
		state = GSTC.api.stateFromConfig(config);
		gstc = GSTC({
			element: this.$refs.gstc,
			state,
		});
	},
	beforeUnmount() {
		if (gstc) gstc.destroy();
	},
	methods: {
		updateFirstRow() {
			state.update(`config.list.rows.${GSTC.api.GSTCID('0')}`, (row) => {
				row.label = 'Changed dynamically';
				return row;
			});
		},
		changeZoomLevel() {
			state.update('config.chart.time.zoom', 21);
		},
	},
};
</script>
<style lang="scss">
@import '@/global';

.gstc-component {
	margin: 0;
	padding: 0;
}

.gstc {
	background: transparent;
	color: white;
	border-color: white;
}

.gstc__chart-calendar,
.gstc__chart-calendar-date-content,
.gstc__chart-timeline-grid-row-cell,
.gstc__chart-calendar-date,
.gstc__list-column-header {
	background: transparent;
	color: white;
	border-color: white;
}

.gstc__chart-timeline-items-row-item {
	background: rgb(168, 45, 0);
}
</style>
